.container-fluid {
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 0; /* Stick to the top of the viewport */
}

body.nav-open {
  overflow: hidden; /* Prevent scrolling when the navigation is open */
}


.hide-vertical-nav {
  width: 0;
  overflow: hidden;
}

.nav-items-scrollable {
  flex: 1;
  overflow-y: auto;
  max-height: calc(100vh - 80px);
}

.nav-items-scrollable::-webkit-scrollbar {
  width: 0;  /* Remove scrollbar width */
  background: transparent;  /* Optional: Set a transparent background */
}



.vertical-nav {
  width: 70px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* Align items to the left */
  position: absolute;
  top: 0;
  bottom: 0;
  z-index: 1; /* Ensure the vertical nav is above the horizontal nav */
  border-right: 1px solid black; /* Add a black border line to the right */
}


.nav-link-vertical {
  padding: 14px; /* Adjust padding as needed */
  display: block;
  text-decoration: none;
  color: black; /* Adjust text color */
}

.nav-link-vertical:hover {
  background-color: lightgray; /* Adjust hover background color */
}

.nav-item {
  list-style-type: none;
}

.nav-item.nav-item-first {
  padding: 10px; /* Add padding to the first item */
  z-index: auto;
}

.horizontal-nav {
  margin-left: 270px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px;
  font-size: 20px;
  position: sticky;
  top: 0; /* Stick to the top of the viewport */
  z-index: 1000; /* Ensure the navigation is above other elements */
  background-color: white; /* Add background color for better visibility */
}

/* Dropdown Styles */
.dropdown-menu {
  min-width: 100px;
  position: absolute;
  z-index: 2;
  margin-left: 20px; /* Add margin to the left */
  padding: 10px; /* Add padding */
}

.dropdown-item {
  display: flex;
  align-items: center;
  padding: 5px 10px; /* Add padding to the dropdown items */
  margin-bottom: 5px; /* Add margin bottom to create space between items */
}

.dropdown-item svg {
  margin-right: 10px; /* Add margin to the right of the icon */
}


/* Adjust padding and margin for horizontal nav links */
.horizontal-nav .nav-link {
  padding: 6px;
  margin-right: 10px;
}

/* Align right side of horizontal navigation links */
.horizontal-nav .right-nav-links {
  display: flex;
  justify-content: flex-end;
}

/* Move the Support button to the left */
.horizontal-nav .left-nav-links {
  display: flex;
  justify-content: flex-start;
}

.flexbox {
  min-height: 100vh;
  margin-left: 270px;
  background-color: whitesmoke;
  border: 200px black;
  padding: 30px;
  display: flex;
  flex-direction: column;
  overflow-y: auto; /* Enable vertical scrolling */
  max-height: calc(100vh - 40px); /* Adjust max-height to fit within viewport minus header */
}

.content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
}

